.socials {
    position: fixed;
    left: -2px;
    top: 40%;
    z-index: 9999;
    display: none;

    @include media-breakpoint-up(md) {
        display: block;
    }

    &__link {
        display: flex;
        width: 42px;
        height: 42px;
        align-items: center;
        justify-content: center;
        background-color: $c-white;
        border: 2px solid $c-white;
        transition: $transition-all-default;
    }

    svg {
        width: 70%;
        fill: $c-white;
    }
}